@import "./reset.css";
@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Poppins:wght@400;500;700&display=swap");

/* 
font-family: 'DM Sans', sans-serif;
font-family: 'Poppins', sans-serif;
*/

/* Правила для всего документа - переменные */
:root {
  --black: #171718;
  --black-border: #26292d;
  --white: #fff;
  --purple: #5c62ec;
  --orange: rgb(242, 164, 7);

  --dark-bg: var(--black);
  --dark-border: var(--black-border);
  --header-text: var(--white);
  --accent: var(--purple);

  /* Light mode */

  --page-bg: var(--white);
  --text-color: var(--black);
  --project-card-bg: var(--white);
  --project-card-text: var(--black);
  --project-card-btn-bg: var(--white);
  --box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25);
}

html,
body {
  min-height: 100vh;

  font-family: "DM Sans", sans-serif;
  letter-spacing: -0.5px;

  /* Light mode */
  background-color: var(--page-bg);
  color: var(--text-color);
}

#root {
  min-height: 100vh;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.dark {
  --page-bg: #252526;
  --text-color: var(--white);
  --project-card-bg: var(--black);
  --project-card-text: var(--white);
  --project-card-btn-bg: var(--accent);
  --box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.8);
}

/* General styles */

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.title-1 {
  margin-bottom: 60px;
  font-size: 60px;
  font-weight: 700;
  line-height: 1.3;
  text-align: center;
  color: var(--accent);
}

.title-2 {
  font-weight: 700;
  font-size: 40px;
  line-height: 1.3;
  text-align: center;
}

/* временно скрыть */
.none {
  display: none !important;
}

/* Dark mode button */

.dark-mode-btn {
  order: 9;
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 51px;
  height: 26px;
  padding: 5px;
  border-color: #272727;
  border-radius: 50px;
}

.dark-mode-btn::before {
  content: "";
  position: absolute;
  top: 1px;
  left: 1px;
  display: block;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: var(--white);
  transition: left 0.2s ease-in;
}

.dark-mode-btn--active::before {
  left: 26px;
}

.dark-mode-btn__icon {
  position: relative;
}

/* Button */

.btn {
  display: inline-block;
  height: 48px;
  padding: 12px 28px;
  border-radius: 5px;
  background-color: var(--accent);
  color: var(--header-text);
  letter-spacing: 0.15px;
  text-align: center;
  font-weight: 500;
  font-size: 16px;
  transition: opacity 0.2s ease-in;
}

.btn:hover {
  opacity: 0.8;
}

/* прожатие кнопки */
.btn:active {
  position: relative;
  top: 1px;
}

/* Projects */

.section {
  padding: 70px 0;
}

.projects {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  row-gap: 30px;
  column-gap: 30px;
}

.project {
  max-width: 370px;
  height: 349px;
  background-color: var(--project-card-bg);
  box-shadow: var(--box-shadow);
  border-radius: 10px;
}

.project img {
  border-radius: 10px;
}

.project__title {
  margin: 15px 73px 25px 20px;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.3;
  color: var(--project-card-text);
}

/* Project detailes */

.project-detailes {
  max-width: 865px;
  margin: 0 auto;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.project-detailes__cover {
  max-width: 100%;
  margin-bottom: 40px;
  filter: drop-shadow(0px 5px 35px rgba(0, 0, 0, 0.25));
  border-radius: 10px;
}
.project-detailes__desc {
  font-weight: 700;
  font-size: 24px;
  line-height: 1.3;
  margin-bottom: 30px;
}

.social {
  display: flex;
  column-gap: 30px;
  column-gap: 30px;
  align-items: center;
}

.social__item {
}

.copyright {
  font-size: 16px;
}

.copyright p + p {
  margin-top: 0.5em;
}

/* Skills */

.content-list {
  margin: 0 auto;
  max-width: 570px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  row-gap: 40px;
  text-align: center;
}

.content-list a {
  color: var(--accent);
}

.content-list__item {
  font-size: 18px;
  line-height: 1.5;
}

.content-list__item p + p {
  margin-top: 0.5em;
}

@media (max-width: 620px) {
  .header {
    min-height: unset;
  }
  .header__title {
    font-size: 30px;
  }
  .header__title strong {
    font-size: 40px;
  }
  .nav-row {
    justify-content: space-between;
  }

  .section {
    padding: 40px 0;
  }

  .social {
    column-gap: 20px;
  }

  .social__item {
    width: 28px;
  }

  .title-1 {
    margin-bottom: 30px;
    font-size: 30px;
  }

  .title-2 {
    font-size: 30px;
    margin-bottom: 10px;
  }

  .project__title {
    font-size: 22px;
  }

  .project-detailes__desc {
    font-size: 22px;
    margin-bottom: 20px;
  }

  .content-list {
    row-gap: 20px;
  }

  .content-list__item {
    font-size: 16px;
  }

  .footer {
    padding: 40px 0 30px;
  }

  .footer__wrapper {
    row-gap: 20px;
  }

  .dark-mode-btn {
    order: 0;
  }
}
